$lgray: #4F4F4F;
$llgray: #808080;

body {
  background-color: #ccc;
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
}
.container {
  max-width: 1200px;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}
.search-btn {
  background-color: transparent;
  border: none;
  cursor: pointer;
  & > img {
    width: 20px;
    height: 20px;

  }
}
.dropdown-list {
  background-color: transparent;
  color: #fff;
  border: 1px solid $llgray;
  cursor: pointer;
  & > option {
    color: #fff;
    cursor: pointer;
  }
}